import React, { Component } from "react";

export default class TodoList extends Component {
  render() {
    const items = this.props.items.filter(function (item) {
      return (
        item.text.includes(this.props.searchValue) ||
        item.taskInformation.includes(this.props.searchValue)
      );
    }, this);
    return (
      <table className="table table-hover">
        <thead>
          <tr>
            <th>Name</th>
            <th>Description</th>
            <th className="text-right">Action</th>
          </tr>
        </thead>
        <tbody>
          {items.map((item, index) => (
            <tr key={item.id}>
              <td className="text-left font-weight-normal">{item.text} </td>
              <td className="text-left text-wrap">{item.taskInformation}</td>
              <td className="text-right">
                <button
                  type="button"
                  href="#"
                  className="btn btn-success btn-sm mr-1 "
                  onClick={() => this.props.editHandler(item, index)}
                >
                  Edit
                </button>
                <button
                  type="button"
                  href="#"
                  className="btn btn-danger btn-sm"
                  onClick={() => this.props.onDelete(item)}
                >
                  Done
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    );
  }
}
